<template>
  <div>
    <div id="app">
      <div class="container">
        <div class="row py-3">
          <div class="col-md-6">
            <h2>產品列表</h2>
            <table class="table table-hover mt-4">
              <thead>
                <tr>
                  <th width="150">產品名稱</th>
                  <th width="120">原價</th>
                  <th width="120">售價</th>
                  <th width="150">是否啟用</th>
                  <th width="120">查看細節</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="item in products" :key="item.id">
                  <td width="150">{{ item.title }}</td>
                  <td width="120">{{ item.origin_price }}</td>
                  <td width="120">{{ item.price }}</td>
                  <td width="150">
                    <span v-if="item.is_enabled" class="text-success"
                      >啟用</span
                    >
                    <span v-else>未啟用</span>
                  </td>
                  <td width="120">
                    <button
                      type="button"
                      class="btn btn-primary"
                      @click="tempShow(item)"
                    >
                      查看細節
                    </button>
                  </td>
                </tr>
              </tbody>
            </table>
            <p>
              目前有 <span>{{ products.length }}</span> 項產品
            </p>
          </div>
          <div class="col-md-6">
            <h2>單一產品細節</h2>
            <template v-if="templateShow.title">
              <div class="card mb-3">
                <img
                  :src="templateShow.imageUrl"
                  class="card-img-top primary-image"
                  alt="主圖"
                />
                <div class="card-body">
                  <h5 class="card-title">
                    {{ templateShow.title }}
                    <span class="badge bg-primary ms-2">{{
                      templateShow.category
                    }}</span>
                  </h5>
                  <p class="card-text">
                    商品描述：{{ templateShow.description }}
                  </p>
                  <p class="card-text">商品內容：{{ templateShow.content }}</p>
                  <div class="d-flex">
                    <p class="card-text me-2">{{ templateShow.price }}</p>
                    <p class="card-text text-secondary">
                      <del>{{ templateShow.origin_price }}</del>
                    </p>
                    元 / {{ templateShow.unit }}
                  </div>
                </div>
              </div>
              <template v-for="img in templateShow.imagesUrl" :key="img.id">
                <img
                  :src="img"
                  alt="副圖
                "
                  class="images m-2"
                />
              </template>
            </template>
            <p class="text-secondary">請選擇一個商品查看</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      url: "https://vue3-course-api.hexschool.io/v2", // 請加入站點
      path: "jpshop", // 請加入個人 API P
      products: [],
      templateShow: {},
    };
  },
  methods: {
    checkLogin() {
      const apiUrl = `${this.url}/api/user/check`;
      axios
        .post(apiUrl)
        .then(() => {
          // console.log(res.data);
          this.getData();
        })
        .catch((err) => {
          console.dir(err);
        });
    },
    getData() {
      axios
        .get(`${this.url}/api/${this.path}/admin/products`)
        .then((res) => {
          console.log(res.data.products);

          this.products = res.data.products;
          console.log(this.products);
        })
        .catch((err) => {
          console.dir(err);
        });
    },
    tempShow(item) {
      this.templateShow = item;
    },
  },
  mounted() {
    const token = document.cookie.replace(
      /(?:(?:^|.*;\s*)hexToken\s*=\s*([^;]*).*$)|^.*$/,
      "$1"
    );
    // axios夾帶 Header 資料一起發送 (token 只需加入一次即可)
    axios.defaults.headers.common["Authorization"] = token;
    // #4  確認是否登入
    this.checkLogin();
    // console.log(token);
  },
};
</script>





